import { MY_ORDER_LIST_FAIL, MY_ORDER_LIST_REQUEST, MY_ORDER_LIST_SUCCESS, ORDER_CREATE_FAIL, ORDER_CREATE_REQUEST, ORDER_CREATE_SUCCESS, ORDER_DELIVER_FAIL, ORDER_DELIVER_REQUEST, ORDER_DETAILS_FAIL, ORDER_DETAILS_REQUEST, ORDER_DETAILS_SUCCESS, ORDER_LIST_FAIL, ORDER_LIST_REQUEST, ORDER_LIST_SUCCESS, ORDER_PAY_FAIL, ORDER_PAY_REQUEST, ORDER_PAY_SUCCESS } from "../Constants/orderConstants"
import axios from 'axios'
export const createOrder= (order)=>async (dispatch,getState)=>{
    try{
       dispatch({
           type:ORDER_CREATE_REQUEST
       })
 
       const {userLogin:{userInfo}}=getState()
       const config={
           headers:{
              'Content-Type':'application/json',    
               Authorization:`Bearer ${userInfo.token}`  
           },
       }
       
       
       const {data}=await axios.post(`/Api/orders`,order,config)
      
 
       dispatch({
         type:ORDER_CREATE_SUCCESS,
         payload:data
     })

    
    }
    catch(error){
       dispatch({
           type:ORDER_CREATE_FAIL,
           payload:error.response &&error.response.data.message?error.response.data.message:error.message,
       })
    }
}




